import React from "react";
import styled from "styled-components";
import { FaTag } from "react-icons/fa";

import ReadingContainer from "../components/ReadingContainer";

const Title = styled.h2`
  align-items: center;
  display: flex;
  font-size: 34px;
  line-height: 1.15;

  @media screen and (max-width: 768px) {
    font-size: 30px;
  }
`;

const Subtitle = styled.div`
  color: rgba(0, 0, 0, 0.54);
  font-size: 23px;
  font-weight: 400;
  line-height: 28px;
  margin-bottom: 30px;

  @media screen and (max-width: 1199px) {
    font-size: 20px;
    line-height: 24px;
    margin-bottom: 10px;
  }
`;

const Tag = ({ title, subtitle, description, posts }) => {
  if (!title) {
    return [<h1>Tag not found</h1>, <a href="/">Bible Answers Home</a>];
  }

  return (
    <div>
      <ReadingContainer>
        <Title>
          <FaTag style={{ marginRight: 6, height: 18, width: 18 }} />
          {title}
        </Title>
        <Subtitle>{subtitle}</Subtitle>
      </ReadingContainer>
    </div>
  );
};

Tag.getInitialProps = async function({ query }) {
  const res = await fetch(
    `http://bibleanswersapi.herokuapp.com/tags/${query.slug}`
  );
  const data = await res.json();

  return data;
};

export default Tag;
